// vmvuex
module.exports = (vm) => {
    // 
    uni.$u.http.setConfig((config) => {
        /* config */
        config.baseURL = 'https://api.shop.eduwork.cn'; /*  */
        return config
    })
	// 
	uni.$u.http.interceptors.request.use((config) => { // async await 
	    // dataundefined{}
	    config.data = config.data || {}
		// customtoken
		if(config?.custom?.auth) {
			// vmvuexvm.$store.state
			config.header.token = 'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJpc3MiOiJodHRwOlwvXC9hcGkudGVzdFwvYXBpXC9hdXRoXC9sb2dpbiIsImlhdCI6MTYwNzUyMDE0MSwiZXhwIjoxNjA3NTIzNzQxLCJuYmYiOjE2MDc1MjAxNDEsImp0aSI6IktVdWFsTmxnOXYzZmlTZHEiLCJzdWIiOjMsInBydiI6IjIzYmQ1Yzg5NDlmNjAwYWRiMzllNzAxYzQwMDg3MmRiN2E1OTc2ZjcifQ.BpVdvBjKEhQ2aIZBfkE-SoU2a3UeFkYCKQKh42Ncbio'
		}
	    return config 
	}, config => { // async await 
	    return Promise.reject(config)
	})
	
	// 
	uni.$u.http.interceptors.response.use((response) => { /*  async await */
		const data = response
		let code = response.statusCode
		// 
		const custom = response.config?.custom
		if (code !== 200) { 
			// customtoastfalsetoast
			if (custom.toast !== false) {
				uni.$u.toast(data.message)
			}

			// catchreject
			if (custom?.catch) {
				return Promise.reject(data)
			} else {
				// pendingpromisecatch
				return new Promise(() => { })
			}
		}
		return data.data === undefined ? {} : data.data
	}, (response) => { 
		//  statusCode !== 200
		return Promise.reject(response)
	})
}